*{
  font-family: Helvetica, Arial, sans-serif;
}
body{
  padding: 60px;
}
input{
  width: 100%;
  font-size: 1.2rem;
  color: gray;
}

.card{
  align-content: center;
  border: 1px solid gray;
  border-radius: 6px;
  overflow: hidden;
  margin-bottom: 6px;
  margin-top: 6px;
}
.card>p{
  padding: 5px;
text-align: center;
}
.card>img{
  max-height: 160px;
}
.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}